@import './config';
@import './footer.scss';
@import './header.scss';
@import './transitions.scss';


/*
Home scss styling
*/


body{
    background-color: $primary-color;
    color: #fff;
    height: 100vh;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    line-height: 1.5;
}

h1, h2, h3 {
    margin: 0;
    font-weight: 400;

    &.lg-heading{
        font-size: 6rem;
    }

    &.sm-heading{
        margin-bottom: 2rem;
        padding: 0.2rem 1rem;
        background: rgba(lighten($color: $primary-color, $amount: 2), 0.5);
    }
}

a{
    color: #fff;
    text-decoration: none;
}

main{
    padding: 4rem;
    height: 100%;

    .icons{
        margin-top: 1rem;


        a{
            padding: 0.4rem;

            &:hover{
                color: $secondary-color;
                @include easeOut();
            }
        }
    }
    
    &#home{
        overflow: hidden;
        h1{
            margin-top: 20vh;
        }

        @include background();
    }
}

.text-secondary{
    color: $secondary-color;
}