@mixin easeOut {
    transition: all 0.5s ease-out;
}

@mixin background {
    @if $show-home-image {
        &#home{
            background: $home-image;
            background-attachment: fixed;
            background-size: cover;
        }

        .overlay {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: rgba($color: $primary-color, $alpha: $background-opacity);
            z-index: 0;
        }

        .content {
            position: relative;
            z-index: 1;
        }
    }
} 